.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.back-to-top .top {
  position: fixed;
  cursor: pointer;
  bottom: 10px;
  right: 15px;
  font-size: 35px;
  color: #ffffff;
  background-color: #000000;
  z-index: 4;
  width: 55px;
  text-align: center;
  height: 45px;
  line-height: 45px;
  border-radius: 30%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.back-to-top .top:hover {
  color: #ffffff;
  background: #571e2c;
}